import React, { Component } from "react";
import { start_add } from "../../lib/libManagerLoading";
import ULoading from "./uLoading";
class applicationLoading extends Component {
  constructor(props) {
    super(props);


    //thời gian tải lâu làm tiến trình báo tải đã xong
    //nhưng chưa tải xong phần tiếp theo
    //nên thêm cờ này để chờ
    this.flagLoaing = true;//true là được biểu diễn loading

    this.state = {
      index_loading: 0,
      list_loading: [{
        title: "Loading...",
        percent: 0,
        ...this.getInfoNetWork()
      }]
    }
    //dù có flagLoaing cũng có lúc bị rớt loading (không ai gọi)!
  }
  setPercent = (str_type_data, system_count, name_info, max_pack, current) => {

    let per = current * 100 / max_pack;

    let title_ = "Loading...";

    let flag_open_loading_ = false;

    if (flag_open_loading_) {
      if (name_info !== undefined)
        title_ += str_type_data + ": " + name_info + "  ... " + system_count;
      else
        title_ += str_type_data + ":  ... " + system_count;
    }

    let total_loading =
      window.process_loading.max.main_script +
      window.process_loading.max.template_script +
      window.process_loading.max.template_component +
      window.process_loading.max.template_css;

    if (total_loading === current)
      per = 100;

    //lưu vào biến
    this.createOneLoading({
      title: title_,
      percent: per,
      ...this.getInfoNetWork()
    });
  }
  callBack_animationPercent = () => {
    let index_loading = this.state.index_loading;

    if (this.state.list_loading[index_loading + 1] !== undefined) {
      index_loading++;
      this.flagLoaing = true;
      this.setState({ index_loading: index_loading });
    }
    else
      this.flagLoaing = false;
  }
  createOneLoading(data_) {
    let list_loading = this.state.list_loading;
    list_loading[list_loading.length] = data_;

    this.setState({ list_loading: list_loading }, () => {
      if (!this.flagLoaing)
        this.callBack_animationPercent();
    });
  }
  getInfoNetWork() {
    if (navigator.connection !== undefined) {
      let effectiveType = "";
      if (navigator.connection.effectiveType !== undefined)
        effectiveType = ": " + navigator.connection.effectiveType;
      let netWorkSpeed = navigator.connection.downlink;
      let mess_ = "";
      if (netWorkSpeed <= 1)
        mess_ = "Internet speed slow";

      let speed_ = {
        title: "Speed test" + effectiveType,
        number_speed: netWorkSpeed + " Mb/s",
      }
      let cmd_ = {
        error: {
          message:
            mess_//Please check your internet connection and try again
        }
      }

      return {
        cmd: cmd_, speed: speed_
      }
    }
    else
      return {
        cmd: {
          error: {
            message:
              ""
          }
        }, speed: {
          title: "",
          number_speed: "",
        }
      }
  }
  reportWindowSize() {
    let device_new = "";
    let window_size = {
      height: window.innerHeight,
      width: window.innerWidth,
    };
    if (window_size.width > 996) device_new = "desktop";
    else if (window_size.width <= 996 && window_size.width > 425)
      device_new = "tablet";
    else
      if (window_size.width <= 425)
        device_new = "mobile"; //nhỏ hơn nữa thì không cần xử lý

    return device_new;
  }
  componentDidMount() {
    start_add(this);

    //mở lại login
    setTimeout(() => {
      document.getElementById('root').style.display = "";
    }, 300);
  }
  render() {
    return <ULoading
      device={this.reportWindowSize()}
      f_callBack_animationPercent={this.callBack_animationPercent}
      dataFull={this.state.list_loading[this.state.index_loading]}></ULoading>;
  }
}
export default applicationLoading;
