export function managerTemplate_autoUui() {
  return (
    createStringID() +
    createStringID() +
    createStringID() +
    createStringID() +
    "-" +
    createStringID() +
    createStringID()
  );
}
const createStringID = () =>
  Math.floor((1 + Math.random()) * 0x10000)
    .toString(16)
    .substring(1);

export function managerTemplate_getComponent(component_code) {
  return window.obManagerTemplate_libManagerTemplate.managerTemplate_getComponent(component_code);
}

export function managerTemplate_getForm(form_code) {
  // const Form = React.lazy(() => import("../template/" + managerTemplate_getTemplate() + "/form/" + form_code));
  // return Form;
  return window.obManagerTemplate_libManagerTemplate.managerTemplate_getForm(form_code);
}

