import React, { Component } from "react";
import { postWebTxCode } from "../../lib/libPost";
import Application from "../../lib/libSupport";
import {
    managerTemplate_getComponent,
} from "../../lib/libManagerTemplate";
class cApplication extends Component {
    constructor(props) {
        super(props);

        //=======01/07/2019==== gọi bo của các app khác, bản đầu tiên=======
        //chỉ riêng componet này mới có chức năng gọi bo bên trong
        //cApplication dùng để tải danh sách ứng dụng
        //xử lý khi người dùng click (là đăng nhập API và các session trên server)
        //sau đó mở với tab mới nếu đăng nhập thành công, ngược lại thì thông báo
        //từ hệ thống 
        //vì component này có thể được gọi bất kỳ nên bo của component này được gọi ở [bo] sys

        this.state = {
            dataFull: {
                title: Application.getLang("login_select_page"),
                data: [],
                cmd: {
                    isLoading: false,
                    isLock: false,
                },
                close_modal: {
                    title: Application.getLang("login_modal_close"),
                    abs_Close: this.abs_Close,
                },
                mess_login: {
                    title: "",
                    type: "success", // "success" / "fail" / "isLoading"
                    app_select_img: ""
                },
                abs_Click: this.clickSelectApp,
            }
        }
    }
    abs_Close = () => {
        let data_full = this.state.dataFull;
        data_full.cmd.isLoading = false;
        data_full.cmd.isLock = false;
        this.setState({ dataFull: data_full });
    }
    appSetStatus_success(mess_login, app_select) {
        let data_full = this.state.dataFull;
        data_full.cmd.isLoading = true;
        data_full.cmd.isLock = true;

        data_full.mess_login.title = app_select.list_application.list_appliaction_name + ": " + Application.getLang(mess_login);
        data_full.mess_login.type = "success";
        data_full.mess_login.app_select_img = app_select.list_application.list_appliaction_img;

        this.setState({ dataFull: data_full });
    }
    appSetStatus_loading(app_select) {
        let data_full = this.state.dataFull;
        data_full.cmd.isLoading = true;
        data_full.cmd.isLock = true;

        data_full.mess_login.title = app_select.list_application.list_appliaction_name;
        data_full.mess_login.type = "isLoading";
        data_full.mess_login.app_select_img = app_select.list_application.list_appliaction_img;

        this.setState({ dataFull: data_full });
    }
    appSetStatus_fail(mess_login, app_select) {
        let data_full = this.state.dataFull;
        data_full.cmd.isLoading = true;
        data_full.cmd.isLock = true;

        data_full.mess_login.title = app_select.list_application.list_appliaction_name + ": " + Application.getLang(mess_login);
        data_full.mess_login.type = "fail";
        data_full.mess_login.app_select_img = app_select.list_application.list_appliaction_img;

        this.setState({ dataFull: data_full });
    }
    componentDidMount() {
        postWebTxCode([{
            txcode: "#sys:bo-projectLogin-listApplication",
            input: {}
        }], {}).then(
            result => {
                var data_ = Application.foGetData("list_application", result);
                //list_application
                //list_appliaction_order
                data_.sort((a, b) => {
                    let a_n = 0;
                    if (a.list_application.list_appliaction_order !== undefined)
                        a_n = parseInt(a.list_application.list_appliaction_order);

                    let b_n = 0;
                    if (b.list_application.list_appliaction_order !== undefined)
                        b_n = parseInt(b.list_application.list_appliaction_order);

                    if (a_n < b_n)
                        return -1;
                    if (b_n < a_n)
                        return 1;
                    return 0;
                });

                let data_to_template = [];
                for (let i = 0; i < data_.length; i++) {
                    let item_ = data_[i];
                    data_to_template[i] = {
                        title: item_.list_application.list_appliaction_name,
                        img: item_.list_application.list_appliaction_img,
                        title_sub:
                            item_.list_application.list_appliaction_des,
                        mess_login: "",
                        dataItem: item_,
                    }
                }
                let data_full = this.state.dataFull;
                data_full.data = data_to_template;
                this.setState({ dataFull: data_full });

                //nếu có parameter b thì sẽ trở về app đã đến
                //nhưng cần thực thi được chức năng đăng nhập của app đó
                //vậy đây là quá trình tìm app và tự đăng nhập [bo]
                var app_code = Application.autoGotoApp_get();
                if (app_code !== undefined && app_code != null) {
                    app_code = app_code.trim();
                    if (app_code !== "") {
                        for (var i = 0; i < data_.length; i++) {
                            if (data_[i]["list_application"] !== undefined) {
                                var list_application_ = data_[i].list_application;
                                if (list_application_ !== undefined)
                                    if (list_application_["list_appliaction_id"] !== undefined)
                                        if (list_application_.list_appliaction_id === app_code) {
                                            this.postApp(list_application_, app_code);
                                            return;//stop all bên dưới
                                        }
                            }
                        }
                    }
                }
                //trường hợp nếu chỉ có 1 app thì quá trình cũng tương tự trên là tự điền mã app để đăng nhập
                if (data_.length === 1) {
                    var list_application_one = data_[0].list_application;
                    if (list_application_one !== undefined)
                        if (list_application_one["list_appliaction_id"] !== undefined)
                            if (list_application_one.list_appliaction_id.trim() !== "") {
                                this.postApp(list_application_one, list_application_one.list_appliaction_id.trim());
                                return;//stop all bên dưới
                            }
                }
            },
            error => {
            }
        );
    }
    clickSelectApp = (item, index) => {
        this.appSetStatus_loading(item);
        postWebTxCode([{
            txcode: "#" + item.list_application.list_appliaction_id + ":" + item.list_application.list_appliaction_bo,
            input: {}
        }], {}).then(
            result => {
                //xử lý tay điều hướng url khi đăng nhập thành công trên server
                //ở đây không lo bị theo dõi, vì session này phải trùng với session trên server
                //thì qua link app mới hoạt động
                //nếu không trùng thì sẽ bị đưa về login lại
                var data_ = Application.foGetData("loginApp", result);
                var lang_ = Application.foGetData("lang", result);
                if (data_ !== undefined) {
                    if (data_["status_login"])
                        if (data_.status_login === "login#127") {
                            this.appSetStatus_success(lang_, item);
                            //07/07/2020
                            //cập nhập chức năng đọc cấu hình template từ url
                            //thông tin này được lấy từ login
                            //việc này giúp cải thiện tốc độ load template (không tốn giai đoạn lấy thông tin template)
                            setTimeout(() => {
                                window.location.replace(Application.gotoApp(item.list_application.list_appliaction_id, item.list_application.configTemplate));
                            }, 700);
                            return;
                        }
                    if (data_["error_login"])
                        this.appSetStatus_fail(lang_, item);
                }
            },
            error => {
            }
        );
    }
    postApp(list_application_, app_code) {
        //đăng nhập vào app bằng bo trước khi chuyển qua app đó
        //nếu thất bại thì cho hiển thị danh sách app để chọn

        postWebTxCode([{
            txcode: "#" + list_application_.list_appliaction_id + ":" + list_application_.list_appliaction_bo,
            input: {}
        }], {}).then(
            result => {
                //xử lý tay điều hướng url khi đăng nhập thành công trên server
                //ở đây không lo bị theo dõi, vì session này phải trùng với session trên server
                //thì qua link app mới hoạt động
                //nếu không trùng thì sẽ bị đưa về login lại
                var data_ = Application.foGetData("loginApp", result);
                if (data_ !== undefined)
                    if (data_["status_login"] !== undefined)
                        if (data_.status_login === "login#127") {
                            window.location.replace(Application.gotoApp(app_code, list_application_.configTemplate));
                            return;
                        }
                setTimeout(() => {
                    this.setState({ isStart: false, });
                }, 2000);
            },
            error => {
            }
        );
    }
    render() {
        const UChooseApp = managerTemplate_getComponent("uChooseApp");
        return <UChooseApp dataFull={this.state.dataFull}></UChooseApp>;
    }
}
export default cApplication;