import React, { Component, Suspense } from "react";
class jWebUITemplateControl extends Component {
    render() {
        //chỉ chạy 1 lần
        let Template = React.lazy(() => import("./" + this.props.template_select + "/" + this.props.screen_working));
        return <Suspense fallback={<div>Loading...</div>}>
            <Template></Template>
        </Suspense>
    }
}
export default jWebUITemplateControl;