class coreTx {

    constructor(context, input, input_action, fo_step, fo_arr_step, input_fo, list_bo_new, fo_arr) {
        if (context === undefined)
            this.context = {};
        else
            this.context = context;

        if (input === undefined)
            this.input = {};
        else
            this.input = input;

        if (input_action === undefined)
            this.input_action = {};
        else
            this.input_action = input_action;

        //14 06 2019 cập nhập chức năng modal confirm (chờ xác nhận)
        //các biến được truyền vào để bẻ được hướng chạy của fo
        //các thông số này không bao giờ trống
        //fo_step: vị trí đang chạy hiện tại
        //input_fo: dữ liệu đã thu thập của input fo từ đầu đến bước chạy hiện tại
        //list_bo_new: dữ liệu đã thu thập được của fo cho ra các bo
        this.fo_step = fo_step;
        this.fo_arr_step = fo_arr_step;
        this.input_fo = input_fo;
        this.list_bo_new = list_bo_new;
        this.fo_arr = fo_arr;
    }

    getInputAction = () => {
        return this.input_action;
    }
    getInput = () => {
        return this.input;
    }
    //thêm dữ liệu vào kho chung
    addPackFo(key, data) {
        if (data !== undefined)
            this.input_action[key] = data;
    }

    //tạo gói roll back để các hệ thống khác gọi và đồng nhất
    buildPackRollBack() {
        //bộ rollback
        return {
            fo_step: this.fo_step,
            fo_arr_step: this.fo_arr_step,
            context: this.context,
            input_fo: this.input_fo,
            input_action: this.input_action,
            list_bo_new: this.list_bo_new,
            fo_arr: this.fo_arr
        };
    }
}
export default coreTx;