import Application from "./libSupport";

//code này đặc thù nên viết dạng ẩn
export function rsa_Encrypted(a) {
	var encrypt = new window.JSEncrypt({ default_key_size: 1024 });
	encrypt.setPublicKey(Application.rsa_getPublicKey());
	var int_slipt = 117;
	var rsa_slip = "";
	var a_cut = a;
	while (a_cut.length > 0) {
		var a__ = "";

		//tính tổng lượng chổi, 
		//nếu 1byte thì là 1
		//nếu 2byte thì là 2
		var utf_length = 0;
		var a_index = 0;
		while (a_cut.length > a_index) {
			//không có kiểm tra chuổi vượt chiều dài không 
			//vì lượng ký tự còn lại có thể nhiều mã utf8 làm chiều dài tuy nhỏ như hơn 116
			var c = a_cut.charCodeAt(a_index);

			if (c < 128)
				utf_length++;
			else if ((c > 127) && (c < 2048))
				utf_length = utf_length + 2;
			else
				utf_length = utf_length + 3;

			if (utf_length >= int_slipt)
				break;
			a__ += a_cut[a_index];
			a_index++;
		}

		a_cut = a_cut.substring(a_index, a_cut.length);
		rsa_slip += encrypt.encrypt(a__);
	}
	return rsa_slip;
}

//thuật giải giải mã cho gói tin trả về của server
//thuật giải này phát triển riêng cho JWebUI
//vì hệ thống RSA không phục vụ cho dữ liệu lớn
//làm tốn tài nguyên của client khi giải mã và lượng kb truyền tải lớn
//p_response là gói tin trả về
//timer_ là mã gồm thời gian + số random để tạo gói tin luôn khác nhau
export function algorithm_response_quick(p_response, timer_) {
	var bitwise_protect = Application.getKeyX(timer_);
	var str_new_decrypt = "";
	for (var iDecrypt = 0; iDecrypt < p_response.length; iDecrypt++)
		str_new_decrypt += String.fromCharCode(p_response.codePointAt(iDecrypt) ^ bitwise_protect);
	return str_new_decrypt;
}

export function rsa_Decrypt(result) {
	//chạy ngược thuật giải để giải chuyển lại gói tin gồm data và private key
	var algorithm = Application.rsa_getAlgorithm();//đang mặc định là 1
	var re_rsa_slip = algorithm_RE_drop(algorithm, result);

	var all_rs = "";
	if (re_rsa_slip) {
		result = re_rsa_slip["data"];
		var private_key = re_rsa_slip["private"];

		var decrypt = new window.JSEncrypt({ default_key_size: 1024 });
		decrypt.setPrivateKey(private_key);

		var max_rsa_ = result.length / 172;// làm tròn giảm

		for (var i_c = 0; i_c < max_rsa_; i_c++)
			all_rs += decrypt.decrypt(result.substring(i_c * 172, (i_c + 1) * 172));
	}
	return all_rs;
}
//hoán đổi theo 1 chiều dài định trước
// eslint-disable-next-line
function algorithm_cross(algorithm, rsa_slip) {
	if (algorithm["cross"] !== undefined) {
		var group_ = algorithm.cross.group;
		var run_ = algorithm.cross.run; // trình tự hoán đổi
		var max_ = algorithm.cross.max;//tối đa 1 nhóm
		//chèn ký tự ; vào mỗi group để có khoản cách
		var max_group_loop = Math.ceil(rsa_slip.length / group_);
		var group_loop = [];
		for (var i = 0; i < max_group_loop; i++) {
			//bắt đầu từ 1
			group_loop[group_loop.length] = rsa_slip.slice(i * group_, (i + 1) * group_);
		}

		//bắt đầu hoán đổi
		var group_loop_tmp = "";//nơi chứa tạm cho hoán đổi

		//round vì phải đủ 4 cập
		var max_group_cross = Math.floor(group_loop.length / max_);//làm tròn giảm
		for (var i_cross = 0; i_cross < max_group_cross; i_cross++) {
			//trong giới hạn này thì hoán đổi
			var arry_from_ = i_cross * max_;

			for (var i_run = 0; i_run < run_.length; i_run++) {
				var from_ = parseInt(run_[i_run].slice(0, 1));
				var to_ = parseInt(run_[i_run].slice(1, 2));
				//có giá trị mới hoán đổi
				//copy to vào tmp
				group_loop_tmp = group_loop[arry_from_ + to_];

				//chép from vào to
				group_loop[arry_from_ + to_] = group_loop[arry_from_ + from_];

				//chép tmp vào from
				group_loop[arry_from_ + from_] = group_loop_tmp;
			}
		}
		return group_loop.join(";");
	}
	return "";
}
// eslint-disable-next-line
function algorithm_RE_drop(algorithm, rsa_slip) {
	var v_drop_ = algorithm["drop"];
	var crack_drop = algorithm["crack"];
	if (v_drop_.length > 0) {
		var str_re_drop = rsa_slip.split(crack_drop);
		if (str_re_drop.length > 0) {
			var str_1 = str_re_drop[0];//chuỗi có dữ liệu của drop: có data và private key
			var str_2 = str_re_drop[1];//chuổi chỉ có dữ liệu -> lấy ra để không bị ảnh hưởng
			var jump_redrop_up = 0;//vị trí chèn vào tăng dần theo thuật giải
			var redrop_index = -1;//vị trí tăng dần theo thuật giải
			var str_1_new = "";//mã private key
			while (jump_redrop_up < str_1.length) {
				redrop_index++;
				if (redrop_index >= v_drop_.length)
					redrop_index = 0;//reset

				jump_redrop_up = jump_redrop_up + v_drop_[redrop_index];

				if (str_1[jump_redrop_up] !== undefined) {
					str_1_new += str_1[jump_redrop_up];

					//thay thế để không bị mất index và sẽ bị xóa 1 lượt
					str_1 = str_1.slice(0, jump_redrop_up) + "]" + str_1.slice(jump_redrop_up + 1);
				}
			}
			str_1 = str_1.replace(/]/g, "");
			return { data: str_1 + str_2, private: str_1_new };
		}
	}
	return {};
}