//hàm không được gọi bất kỳ
//kiểm tra phân quyền chỉ cần bỏ vào
export const buildRoleOfForm = (form_code) => {
	var ob_role = getRole(form_code);

	//danh sách các component có quyền
	//vì có thể 1 user sở hữu nhiều quyền nên các quyền ở true sẽ được ưu tiên
	var list_component = {};
	for (var i = 0; i < cache.user.roles.length; i++) {
		if (ob_role[cache.user.roles[i]] !== undefined) {
			//có quyền
			var has_role = ob_role[cache.user.roles[i]];
			for (var xx in has_role) {
				if (list_component[xx] === undefined)
					list_component[xx] = has_role[xx];//mới
				else {
					//lấy ra để so sánh
					var role_compare = list_component[xx];
					//đã có nên kiểm trả ưu tiên là true
					//role
					if (has_role[xx]["role"] !== undefined) {
						for (var x_role in has_role[xx]["role"])
							if (has_role[xx]["role"][x_role])//bằn true thì đè đại
								role_compare["role"][x_role] = true;
					}
					//if (has_role[xx]["property"] !== undefined) {

					//}
					list_component[xx] = role_compare;
				}
			}
		}
	}
	cache.config.role_right[form_code] = list_component;
}

