	//chỉ xóa được các form đang mở
	//xóa các form theo phiên bản
	//việc này có thể thực hiện tại list tab form đang mở (PagerTab)
	static removeForm(form_key) {
		window.alert("Chức năng đang lỗi, vui lòng đừng dùng");
		if (form_key !== "") {
			var form_current = this.getFormCurrent();
			var string_key_form = "";
			if (form_current.hasOwnProperty("getKeyFormAuto")) {
				string_key_form = form_current.getKeyFormAuto();
			}
			//tìm form đã mở, thường điều này chắc chắc
			var iii = 0;
			var x_old = "";
			for (var x in cache.form.auto_form) {
				if (cache.form.auto_form[x].hasOwnProperty("callSetHidden")
					&& cache.form.auto_form[x].hasOwnProperty("getKeyFormAuto"))
					if (cache.form.auto_form[x].getKeyFormAuto() === form_key) {

						//xóa form đã mở tìm được
						delete cache.form.auto_form[x];

						//xóa trong list DOM
						cache.form.opening_forms.splice(iii, 1);

						//chạy load lại giao diện làm việc
						ReactDOM.render(<div>{cache.form.opening_forms}</div>, document.getElementById('content'));

						//nếu form xóa là form đang mở
						if (string_key_form === form_key) {
							//lấy form tiếp theo gần nhất
							if (cache.form.opening_forms.length > 0) {
								this.setFormCurrent(cache.form.auto_form[x_old]);
							}
							else {
								//không còn form này thì show DASHBOARDS
								//form mặc định

							}
						}//ngược lại thì không cần quan tâm
						return;
					}
				x_old = x;
				iii++;
			}

		}
		else {
			//đóng tất cả
			cache.form.auto_form = {};
			cache.form.opening_forms = [];
			ReactDOM.render(<div>{cache.form.opening_forms}</div>, document.getElementById('content'));
		}
	}
