	//lấy thông tin ngôn ngữ với dữ liệu
	//mục đích cho ra các thông báo động
	//vd: Đã lưu thành công dữ liệu [title]
	//title được khai báo trong mapping (ánh xạ) của designDatabase
	static getLangWithData(keyLang, data) {
		var lang_ = getLang(keyLang);
		if (data !== undefined) {
			if (data["table"] !== undefined && data["data"] !== undefined) {
				//data của dữ liệu {}
				var data_ob = data.data;
				//lấy cấu trúc table ra
				var table_ = getDataBaseTable(data.table);
				if (table_ !== undefined)
					if (table_["detail"] != null) {
						var table_detail = table_["detail"];
						for (var i_col = 0; i_col < table_detail.length; i_col++) {
							var table_detail_col = table_detail[i_col];
							if (table_detail_col["code"] != null && table_detail_col["mapping"] != null) {
								if (data_ob[table_detail_col.code] !== undefined) {
									lang_ = lang_.replace("[" + table_detail_col.code + "]", data_ob[table_detail_col.code]);
									lang_ = lang_.replace("[" + table_detail_col.mapping + "]", data_ob[table_detail_col.code]);
								}
								else {
									lang_ = lang_.replace("[" + table_detail_col.code + "]", "");
									lang_ = lang_.replace("[" + table_detail_col.mapping + "]", "");
								}
							}
						}
					}
			}
		}
		return lang_;
	}
	static supportFormatDate(date_long) {
		var now = new Date(parseFloat(date_long));
		return now.toLocaleDateString() + " " + now.toLocaleTimeString();
	}

	//support cho fo txcode để chuyển đổi từ cấu hình xuyên ứng dụng thành cấu hình của ứng dụng đó
	//dành cho fo_check
	static convertFoOfApp_fo_check(fo_check) {
		//do fo_check là thành phần có key fo làm key chính nên cần chuyển key nữa
		//tạo 1 đối tượng mới
		var fo_check_new = {};

		//chuyển data
		for (var x in fo_check)
			if (fo_check[x].includes("#")
				&& fo_check[x].includes(":")) {
				var tx_code_new = x.substring(x.indexOf(":") + 1, x.length);
				fo_check_new[tx_code_new] = tx_code_new;
			}
			else
				fo_check_new[x] = x;//gọi của app hiện tại
		return fo_check_new;
	}
	//dành cho fo_arr
	static convertFoOfApp_fo_arr(fo_arr) {
		var for_arr_new = [];
		for (var i = 0; i < fo_arr.length; i++)
			if (fo_arr[i]["txcode"] !== undefined) {
				for_arr_new[i] = JSON.parse(JSON.stringify(fo_arr[i]));
				if (fo_arr[i].txcode.includes("#")
					&& fo_arr[i].txcode.includes(":")) {
					var tx_ = fo_arr[i].txcode;
					for_arr_new[i].txcode = tx_.substring(tx_.indexOf(":") + 1, tx_.length);
				}
			}
		return for_arr_new;
	}

	//bộ hàm hỗ trợ tương tác dữ liệu toàn cùng
	//đẩy 1 lần vào đúng table cần thiết
	static setData(table, o) {
		cache.data[table] = o;
	}

	//thêm data vào
	static addData(table, ob_data) {
		if (cache.data[table] === undefined)
			cache.data[table] = [];
		cache.data[table].unshift(ob_data);
	}

	//chỉnh sửa data
	static updateData(table, ob_data, table_pk) {
		if (table_pk === undefined)
			table_pk = "AFGHkeyIndexWJ";

		if (cache.data[table] === undefined) {
			cache.data[table] = [];
			return;//không có để sửa
		}
		if (ob_data[table] !== undefined)
			for (var i = 0; i < cache.data[table].length; i++) {
				if (cache.data[table][i][table][table_pk] !== undefined
					&& ob_data[table][table_pk] !== undefined) {
					if (cache.data[table][i][table].AFGHkeyIndexWJ === ob_data[table].AFGHkeyIndexWJ) {
						cache.data[table][i] = ob_data;
						return; // xóa xong
					}
				}
			}
	}


	static deleteData(table, ob_data, pk_col) {
		if (pk_col === undefined)
			pk_col = "AFGHkeyIndexWJ";

		if (cache.data[table] === undefined) {
			cache.data[table] = [];
			return; // không có để xóa
		}

		if (ob_data[table] !== undefined)
			for (var i = 0; i < cache.data[table].length; i++) {
				if (cache.data[table][i][table][pk_col] !== undefined
					&& ob_data[table][pk_col] !== undefined) {
					if (cache.data[table][i][table][pk_col] === ob_data[table][pk_col]) {
						cache.data[table].splice(i, 1);
						return; // xóa xong
					}
				}
			}
	}
