	//tìm data theo data_key_value từ AFGHkeyIndexWJ
	static findData(table, data_key, data_key_value) {
		if (cache.data[table] === undefined) {
			cache.data[table] = [];
			return undefined;
		}
		for (var i = 0; i < cache.data[table].length; i++) {
			if (cache.data[table][i][table][data_key] !== undefined) {
				if (cache.data[table][i][table][data_key] === data_key_value)
					return cache.data[table][i];
			}
		}
	}
	//tìm và ghi mới dữ liệu này vào kho
	static findData_Update(data_, pk_col) {
		if (pk_col === undefined)
			pk_col = "AFGHkeyIndexWJ";

		if (data_ !== undefined)
			for (var x in data_) {
				if (data_[x][pk_col] !== undefined) {
					if (cache.data[x] === undefined)
						cache.data[x] = [];

					//tìm
					for (var i = 0; i < cache.data[x].length; i++)
						if (cache.data[x][i][x][pk_col] !== undefined)
							if (cache.data[x][i][x][pk_col] === data_[x][pk_col]) {
								cache.data[x][i] = data_;
								return;
							}

				}
			}
	}

