	/**      
   
	* @param {string} code_error mã lỗi    
	* @param {JSON} info_exception data đang xử lý. ex: { form_code: "nnnnn" }
	*  * Xử lý lỗi, đây làm hàm thuộc hệ thống, 
	* chuyên cho việc xử lý lỗi theo mã
	* các mã được quy định trong tài liệu
	* Các trường hợp dùng:
	* a. Ngoại lệ: try catch
	* b. Ở mệnh đề if nhưng else thường ít xảy ra
	* c. Lỗi truy xuất data (chưa có khái niệm rõ ràng)
	* Tự động: tùy code_error sẽ có thể thông báo đến kênh notify tương ứng
	* */
	//type_error loại dữ liệu: như 
	//data: là thuộc dữ liệu,
	//system: là thuộc hệ thống, thường mức ưu tiên cao,
	//flow: thuộc quy trình nghiệp vụ người dùng
	static AppException(code_error, info_exception, type_error) {
		Application.log(type_error + "=AppException=>" + Application.getLang(code_error) + "=========" + info_exception);
	}
