import basicTxCode from "../tx/basicTxCode";
import Application from "./libSupport";
import { postWebTxCode } from "./libPost";
export default class libClient {
    static quickPost(bo_, context) {
        postWebTxCode(bo_, context).then(
            result => {
            },
            error => {
            }
        );
    }
    static fo(fo_arr, context) {
        // danh sách tất cả fo yêu cầu xử lý
        //1 kiểm tra tất cả config txCode fo yêu cầu có sẵn chưa?
        var fo_check = {};

        //xử lý fo_arr
        //3/7/2019 xóa đi các nội dung quy định gọi xuyên app
        //xóa cú pháp #app:txcode chỉ còn txcode
        //việc fo_arr_down đã được thiết lập #app:txcode
        var fo_arr_no_cross_app = Application.convertFoOfApp_fo_arr(fo_arr);

        //  console.log("fo_arr_no_cross_app=>" + JSON.stringify(fo_arr_no_cross_app));
        //vì lý do 2 thành phần cross app và chính app chạy chung
        //nên quá trình chuyển đổi txcode cần 2 bước

        //bước 1: lưu vào danh sách đen đã tải để không bị vòng lập vô hạng
        for (var i_1 = 0; i_1 < fo_arr_no_cross_app.length; i_1++) {
            var ob_ = fo_arr_no_cross_app[i_1];
            if (ob_["txcode"] !== undefined) {
                if (!Application.BlackList_check("tx_fo", ob_.txcode))
                    return;//dừng tất cả hành động không cần thông báo gì thêm

                var ob_fo_check = fo_arr[i_1];
                if (ob_fo_check["txcode"] !== undefined)
                    if (Application.getTxFo(ob_.txcode) === undefined)
                        fo_check[ob_fo_check.txcode] = ob_fo_check.txcode;//không bao giờ trùng
            }
        }


        //2 lọc ra danh sách các config txCode fo chưa có, tiến hành yêu cầu tải về rồi mới xử lý
        var fo_arr_down = "";
        for (var x_tx_fo in fo_check)
            if (fo_arr_down === "")
                fo_arr_down = x_tx_fo;
            else
                fo_arr_down += ";" + x_tx_fo;

        fo_check = Application.convertFoOfApp_fo_check(fo_check);
        fo_arr = Application.convertFoOfApp_fo_arr(fo_arr);

        if (fo_arr_down === "")
            this.runTx(fo_arr, context)
        else {
            postWebTxCode([{
                txcode: "#sys:bo-load-txFo",
                input: { list_tx: fo_arr_down }
            }], {}).then(
                result => {
                    //đây là trường hợp ngoại lệ của fo, chỉ có trong libClient
                    //1 luôn luôn trả về 1
                    if (result["fo"] !== undefined)
                        if (result.fo.length > 0)
                            if (result.fo[0]["input"] !== undefined) {
                                var data_bo = result.fo[0]["input"];
                                if (data_bo["txClient"] !== undefined)
                                    for (var i_new = 0; i_new < data_bo.txClient.length; i_new++)
                                        if (data_bo.txClient[i_new]["txcode"] !== undefined &&
                                            data_bo.txClient[i_new]["actions"] !== undefined) {
                                            delete fo_check[data_bo.txClient[i_new].txcode];
                                            Application.addTxFo(data_bo.txClient[i_new].txcode, data_bo.txClient[i_new]);
                                        }
                            }

                    //thông báo admin mail khi không có txCode 
                    var tx_no_config = "";
                    for (var x_tx_no in fo_check) {
                        Application.BlackList_add("tx_fo", x_tx_no);
                        if (tx_no_config === "")
                            tx_no_config += x_tx_no;
                        else
                            tx_no_config += ";" + x_tx_no;
                    }

                    if (tx_no_config !== "")
                        Application.AppException("#libClient001", tx_no_config, "system");
                    else
                        this.runTx(fo_arr, context);
                },
                error => {
                }
            );
        }
    }
    //11 06 update chuyển flow client thành bo/fo
    //hàm này chỉ bị gọi bởi các component của cấu hình động vì nó liên quan môi trường tự động
    //không được gọi như 1 hàm bất kỳ
    //nghĩa là nếu dùng thì nên thông báo ThanhLM
    static runActionWithContext(actions, data, infoA, infoA_parent, componentConfigDefault) {
        if (infoA !== undefined && componentConfigDefault !== undefined) {
            //cấu trúc JSON chung cho tất cả tham số,
            //bản này vừa cập nhập để chuẩn hóa code
            //cấu trúc
            var parameter = {
                infoA: infoA,//{} thông tin form gọi RULE này
                infoA_parent: infoA_parent,//{} thông tin form gọi RULE, nhưng của form cha
                componentConfigDefault: componentConfigDefault,//{} thông tin component gọi RULE này
                data: data
            }

            //tự tạo 1 mã tx_code không có trong hệ thống để chạy 
            //và chống trùng mã với các button khác
            var tx_code = infoA.key_form + componentConfigDefault.codeHidden;
            Application.addTxFo(tx_code, {
                input: {},
                actions: actions,
                status: "A",
                txtype: "bo",
                txcode: tx_code,
                txname: "",
            });
            this.runTx([{
                txcode: tx_code,
                input: {}
            }], parameter);
        }
    }
    //hàm này chỉ được gọi từ form auto
    //không được gọi như 1 hàm bất kỳ
    //nghĩa là nếu dùng thì nên thông báo ThanhLM
    static runFoWithContext(fo_arr, data, infoA, infoA_parent, componentConfigDefault) {
        if (infoA !== undefined && componentConfigDefault !== undefined) {
            //cấu trúc JSON chung cho tất cả tham số,
            //bản này vừa cập nhập để chuẩn hóa code
            //cấu trúc
            var parameter = {
                infoA: infoA,//{} thông tin form gọi RULE này
                infoA_parent: infoA_parent,//{} thông tin form gọi RULE, nhưng của form cha
                componentConfigDefault: componentConfigDefault,//{} thông tin component gọi RULE này
                data: data
            }

            //hàm này có chức năng chạy 1 fo hoàn chỉnh
            this.fo(fo_arr, parameter);
        }
    }
    //chạy 1 tx fo khi biết trong kho Application luôn có tx fo này
    //việc nạy như vầy giúp hệ thống nền có thể run trực tiếp tx fo

    //rollback_ là 1 siêu biến, dùng cho việc khôi phục lại vị trí đang chạy của fo
    //mặc định rollback_=undefined
    static runTx(fo_arr, context, rollback_) {
        var fo_arr_step = 0;

        //giúp việc chạy rollBack chỉ 1 lần, các lần sau là như quá trình bình thường
        var flag_rollBack = true;

        //khôi phục
        if (rollback_ !== undefined && flag_rollBack) {
            fo_arr_step = rollback_["fo_arr_step"];
            fo_arr = rollback_["fo_arr"];
            context = rollback_["context"];
        }

        for (var i = fo_arr_step; i < fo_arr.length; i++) {
            var ob_ = fo_arr[i];
            //chạy từng fo
            if (ob_["txcode"] !== undefined) {
                var fo_input = {};

                //input của tx fo là bộ tham số được thay đổi lại qua mỗi lần chạy action
                //nên cần copy trước
                if (ob_["input"] !== undefined)
                    fo_input = ob_["input"];

                //lấy cấu hình của fo theo txcode
                var config_fo = Application.getTxFo(ob_.txcode);
                if (config_fo !== undefined)
                    if (config_fo["actions"]) {
                        var list_actions = config_fo["actions"];
                        //chạy danh sách action của từ fo
                        //khởi tạo biến lưu thông tin dữ liệu tổng hợp

                        //từ từng fo, được cập nhập thêm từ input_action
                        var input_fo = {};

                        //danh sách bo cần gửi lại server
                        var list_bo_new = [];

                        //vị trí bắt đầu chạy fo
                        var fo_step = 0;

                        //khôi phục
                        if (rollback_ !== undefined && flag_rollBack) {
                            input_fo = rollback_["input_fo"];
                            fo_step = rollback_["fo_step"];
                            list_bo_new = rollback_["list_bo_new"];
                        }

                        for (var i_action = fo_step; i_action < list_actions.length; i_action++)
                            //phân tích trường function là gồm gói và hàm
                            //vd system.startLang
                            if (list_actions[i_action]["function"] !== undefined) {
                                var function_ = list_actions[i_action].function.split(".");
                                if (function_.length >= 2) {
                                    //dữ liệu trả về bởi action
                                    var rs_val = undefined;

                                    //từ từng hàm, thay đổi sau khi chạy xong hàm
                                    var input_action = {};

                                    //khôi phục
                                    if (rollback_ !== undefined && flag_rollBack) {
                                        rs_val = rollback_["val_select"];
                                        input_action = rollback_["input_action"];
                                    }

                                    //giá trị rs_val = undefined nghĩa là vẫn chạy như bình thường quy trình
                                    if (rs_val === undefined) {
                                        //3 tất cả config txCode fo có đủ, chạy xử lý từng config txCode fo 
                                        var _code_system = null;

                                        try {
                                            _code_system = new basicTxCode[function_[0].trim()](context, ob_.input, input_action, i_action, i, input_fo, list_bo_new, fo_arr);
                                        }
                                        catch (e) {
                                            Application.AppException("#libClient003", function_[0].trim(), "system");
                                        }

                                        if (_code_system === null)
                                            return;//stop all

                                        if (_code_system.hasOwnProperty(function_[1].trim())) {
                                            if (list_actions[i_action]["para"] !== undefined) {
                                                var para_ = list_actions[i_action]["para"];
                                                //chuyển đổi para thành dữ liệu từ input và config

                                                var fo_input_new = Application.supportReplaceFo(Application.cloneJson(fo_input), {});
                                                for (var i_para_ = 0; i_para_ < para_.length; i_para_++) {
                                                    var key_para_ = para_[i_para_];
                                                    if (key_para_.length > 0) {
                                                        //có @ là thu thập từ data của fo_input
                                                        if (key_para_.substring(0, 1) === "@")
                                                            para_[i_para_] = supportGetData(key_para_.substring(1, key_para_.length), fo_input_new);
                                                        //có $ là thu thập từ toàn cục của hệ thống cache

                                                        //có # là thu thập từ data của context.data
                                                    }
                                                }

                                                rs_val = _code_system[function_[1].trim()].apply(_code_system, Array.prototype.slice.call(para_, 0));
                                            }
                                            else
                                                rs_val = _code_system[function_[1].trim()]();
                                        }
                                        else
                                            if (Application.BlackList_check("function", list_actions[i_action].function)) {
                                                Application.BlackList_add("function", list_actions[i_action].function);
                                                Application.AppException("#libClient002", list_actions[i_action].function, "system");
                                            }
                                        //vì javascript không truyền được tham biến vào hàm
                                        //nên lấy lại bằng cách này                                      
                                        input_action = _code_system.getInputAction();
                                    }
                                    if (rs_val !== undefined) {
                                        //mệnh lệnh đặc biệt
                                        //systemStop bắt buộc dừng mới mọi lý do
                                        if (rs_val === "systemStop")
                                            return;

                                        rs_val = rs_val.toString();
                                    }
                                    else
                                        rs_val = "_default_";

                                    //tổng hợp các dữ liệu từ input_action và input_fo
                                    if (input_action !== undefined)
                                        for (var x_input_action in input_action)
                                            input_fo[x_input_action] = input_action[x_input_action];

                                    //nếu có request thì tạo gói bo
                                    if (list_actions[i_action]["request"] !== undefined) {

                                        //tìm có cmd không?
                                        if (rs_val !== undefined)
                                            if (list_actions[i_action].request[rs_val] !== undefined)
                                                if (list_actions[i_action].request[rs_val]["cmd"] !== undefined) {
                                                    var cmd_val = list_actions[i_action].request[rs_val]["cmd"];
                                                    if (cmd_val === "stop")
                                                        return;//stop all                                                    
                                                }
                                        this.buildBoFormAction(list_bo_new, list_actions[i_action]["request"], rs_val, input_action, undefined, fo_input);
                                    }
                                }
                                flag_rollBack = false;
                            }

                        //yêu cầu final, là yêu cầu tự thực thi khi tất cả hàm trình tự chạy xong
                        if (config_fo["request"] !== undefined)
                            this.buildBoFormAction(list_bo_new, undefined, undefined, input_fo, config_fo["request"], fo_input)

                        if (list_bo_new !== undefined)
                            if (list_bo_new.length > 0)//yêu cầu bo xử lý tiếp
                                this.quickPost(list_bo_new, context);
                    }
            }
        }
    }
    //tạo các bo và lưu vào danh sách bo cần gọi
    //khi request_final có giá trị thì nó là cuối cùng, chỉ có 1
    static buildBoFormAction(list_bo_new, action_request, rs_val, input_action, requset_final, fo_input) {
        //tìm ra danh sách bo cho request dự vào kết quả của action
        var fo_action = undefined;

        if (requset_final === undefined) {
            if (rs_val !== undefined) {
                if (action_request[rs_val] !== undefined)
                    fo_action = action_request[rs_val];
            }

            if (fo_action === undefined)
                if (action_request["_default_"] !== undefined)
                    fo_action = action_request["_default_"];   // _default_
        }
        else
            fo_action = requset_final;

        if (fo_action !== undefined)
            if (fo_action["bo"] !== undefined)
                if (fo_action["bo"].length > 0)
                    for (var i = 0; i < fo_action["bo"].length; i++) {
                        var ob_ = fo_action["bo"][i];
                        if (ob_["txcode"] !== undefined)
                            if (ob_.txcode.trim() !== "") {
                                var input_bo_new = {};

                                //chép data
                                for (var x_data in input_action)
                                    input_bo_new[x_data] = input_action[x_data];

                                //chép config
                                //công thêm tham số và dữ liệu thành 1
                                if (ob_["input"] !== undefined) {
                                    var bo_input_config = ob_["input"];
                                    bo_input_config = this.loopRepaceData(bo_input_config, fo_input);
                                    for (var x_config in ob_["input"])
                                        input_bo_new[x_config] = ob_["input"][x_config];
                                }

                                //thêm danh sách chờ
                                list_bo_new[list_bo_new.length] = {
                                    txcode: ob_.txcode,
                                    input: input_bo_new
                                }
                            }
                    }
    }

    //đệ quy quét hết cây json
    //hàm thay thế input_bo trước khi trộn với input data
    //các dữ liệu thay thế được lấy từ 
    static loopRepaceData(json_config, fo_input) {
        if (json_config !== undefined && fo_input !== undefined) {
            for (var x in json_config) {
                switch (Object.prototype.toString.call(json_config[x])) {
                    case "[object String]":
                        var str_ = json_config[x];
                        if (str_.length > 0)
                            if (str_.substring(0, 1) === "@")
                                json_config[x] = MapS(str_.substring(1, str_.length), fo_input);
                        break;
                    case "[object Number]":
                        break;
                    case "[object Boolean]":
                        break;
                    case "[object Array]":
                        //chạy đệ quy
                        if (json_config[x].length > 0)
                            for (var i_ = 0; i_ < json_config[x].length; i_++)
                                json_config[x][i_] = this.loopRepaceData(json_config[x][i_], fo_input);
                        break;
                    case "[object Object]":
                        //chạy đệ quy
                        json_config[x] = this.loopRepaceData(json_config[x], fo_input);
                        break;
                    default:
                        //không xử lý gì
                        break;
                }
            }
        }
        return json_config;
    }

}
//hàm có tác dụng lấy dữ liệu theo cấu trúc .
//hình thức nhiều cấp cho data_
//data:{a:"b"} thì key_=data.a
export const supportGetData = (key_, data_) => {
    if (key_.includes(".")) {
        var key_data = key_.split(".")
        if (key_data.length > 1) {
            var data_rs = data_;
            for (var i = 0; i < key_data.length; i++) {
                if (data_rs[key_data[i]] !== undefined)
                    data_rs = data_rs[key_data[i]];
                else
                    return undefined;//không có
            }
            //chạy đến cuối cùng là giá trị cần lấy
            //làm cách này có thể trả về nguyên bản của dữ liệu json
            return data_rs;
        }
    }
    else
        return data_[key_];
    return undefined;

}
//có @
//thì mặc định gọi hàm này
export const MapS = (key, json_data) => {
    return json_data[key];
}