import Application, { getKey_D, getWebSV, getLangKey, getKey_APP, getUserID } from "./libSupport";
class wizA {
	constructor() {
		//03 06 gó bo cho txCode
		this.bo = [];

		//tự tìm và thiết lập các thông số
		const now = new Date();
		this.info = {
			d: getKey_D(),
			u: getWebSV(),
			lang: getLangKey(),
			app: getKey_APP(),
			uid: getUserID(),
			my_device: Application.getMyDevice(),
			request_time: now.getTime()
		}
		//bật chức năng debug có nghĩa các lỗi sẽ được ghi lại vào 
		//bo_trace và fo_trace
		//xem cùng lúc với show trace (sẽ có thêm 2 thông tin trên)
		if (Application.isDebug())
			this.info["dgu"] = Application.isDebug();
	}

	setBo(bo, key_feedback) {
		//Mô tả Feedback
		//Chức năng gồm 3 phần:
		//1. Tự đánh dấu gói tin: khi 1 fo/bo được gọi sẽ bị đánh dấu 1 mã và mã này sẽ xuyên suốt cả gói tin đó
		//vd: click 1 button được đánh 1 mã là asdd-35345-fdfa-234 (gọi là mã Feedback) thì khi gửi thông tin lên API, SQL và trả về điều được lưu lại vào thư mục mã Feedback.
		//2. Khi thông báo lỗi validate từ component sẽ xuất hiện 1 button (có từ đến), click vào sẽ scroll đến vị trí component đó (nếu component đó trong tab, mutivalue) thì sẽ tự mở hoặc click mở tab đó.
		//3. Thông báo từ logic application, có 1 form [quy định mối liên kết giữa từ khóa trên thông báo] và component code và tên 1 tx_fo xử lý (hàm trong tx)
		//Hàm tx_fo có thể chuyển đổi từ thông báo thành từ khóa, từ từ khóa jwebui sẽ tra vào [quy định mối liên kết giữa từ khóa trên thông báo] để đến component như ở mục 2. Nếu không có component code thì button sẽ không hiện ra.

		//Chức năng feedback khi người dùng chọn sẽ tự thu thập cả 3 thông tin trên + dữ liệu form động đang mở => gửi vào tài khoản mail của user hỗ trợ chính hệ thống jwebui. (sẽ nghiên cứu thêm chức năng chụp hình)
		if (key_feedback !== undefined)
			this.info["key_feedback"] = key_feedback;
		this.bo = bo;
	}

	//chỉ lấy gói tin theo cấu trúc bo (txCode)
	getForTxCode() {
		//30 08 2019
		//mở chức năng checksum cho các gói tin
		//giải thuật checksum của jwebui
		//mục tiêu checksum cả gói tin, loại trừ mã checksum
		//bước 1: tạo gói tin hoàn chỉnh
		var p = {
			info: this.info,
			bo: this.bo
		};

		var str_p = JSON.stringify(p);

		//bước 2 gán mã checksum vào gói tin lại
		p.info["checksum"] = this.checkSum(this.stringToArray(str_p));
		return p;
	}

	checkSum(a) {
		var len = a.length,
			fnv = 0;
		for (var i = 0; i < len; i++)
			fnv = (fnv + (((fnv << 1) + (fnv << 4) + (fnv << 7) + (fnv << 8) + (fnv << 24)) >>> 0)) ^ (a[i] & 0xff);
		return fnv >>> 0;
	};
	stringToArray(str) {
		var utf8 = [];
		for (var i = 0; i < str.length; i++) {
			var charcode = str.charCodeAt(i);
			if (charcode < 0x80) utf8.push(charcode);
			else if (charcode < 0x800) {
				utf8.push(0xc0 | (charcode >> 6),
					0x80 | (charcode & 0x3f));
			}
			else if (charcode < 0xd800 || charcode >= 0xe000) {
				utf8.push(0xe0 | (charcode >> 12),
					0x80 | ((charcode >> 6) & 0x3f),
					0x80 | (charcode & 0x3f));
			} else { // surrogate pair
				i++;
                /**
                 * UTF-16 encodes 0x10000-0x10FFFF by
                 * subtracting 0x10000 and splitting the
                 * 20 bits of 0x0-0xFFFFF into two halves
                 */
				charcode = 0x10000 + (((charcode & 0x3ff) << 10)
					| (str.charCodeAt(i) & 0x3ff))
				utf8.push(0xf0 | (charcode >> 18),
					0x80 | ((charcode >> 12) & 0x3f),
					0x80 | ((charcode >> 6) & 0x3f),
					0x80 | (charcode & 0x3f));
			}
		}

		if (this.hasTyped) {
			return new Uint8Array(utf8);
		}

		return utf8;
	};
	hasTyped() {
		if (!('Uint8Array' in window)) {
			return false;
		}

		try {
			var a = new window.Uint8Array(10);
			a[0] = 100;
			if (a[0] === 100) {
				return true;
			}
			return false;
		} catch (e) {
			return false;
		}
	};
}
export default wizA;