import React, { Component } from "react";
import { postWebTxCode } from "../../lib/libPost";
import Application from "../../lib/libSupport";
import {
  managerTemplate_getComponent,
} from "../../lib/libManagerTemplate";
class Login extends Component {
  constructor(props) {
    super(props)
    let data_list_lang = Application.getConfigUser("list_lang_config");

    var url_parameter = new URL(window.location.href);
    var app_code = url_parameter.searchParams.get("b");
    let list_app = Application.getData("list_appliaction");
    let app_choose_ = {
      isChoose: false,
      img: "",
      title: "",
      abs_Close: this.btnRemoveRememberApp,
    }
    for (let ij = 0; ij < list_app.length; ij++) {
      let item_app = list_app[ij].list_appliaction;
      if (item_app.list_appliaction_id === app_code) {
        app_choose_.isChoose = true;
        app_choose_.img = item_app.list_appliaction_img;
        app_choose_.title = item_app.list_appliaction_name;
        Application.autoGotoApp_set(app_code);
      };
      break;
    }

    let data_to_template = [];
    if (data_list_lang !== undefined)
      for (var i = 0; i < data_list_lang.length; i++) {
        let item_ = data_list_lang[i];
        data_to_template[i] = {
          title: item_.view,
          selected: item_.selected ? true : false,
          img: item_.img,
          dataItem: item_,
        }
      }

    let dataFull_ = {
      img_logo: {
        value: "aaaa",
        hyper_link: "",
      },
      title: "",
      title_sub: "",
      title_sub1: "",
      form_error: {
        message: "",
      },
      config: { visible: true },
      email: {
        title: "",
        placeholder: "",
        value: "",
        abs_Change: this.changeInputEmail
      },
      password: {
        title: "",
        placeholder: "",
        value: "",
        abs_Change: this.changeInputPassword
      },
      button_submit: {
        dataFull: {
          cmd: { isLoading: false, isLock: false },
          config: {
            default: {
              title: "",
              type: "primary",
              class: "col-12",
            },
          },
        },
        abs_Click: this.onLogin,
      },
      lang: {
        data: data_to_template,
        abs_Click: this.changeLang,
      },
      app_choose: app_choose_
    }
    this.state = {
      form_error: "",
      dataFull: this.buildDataFromLang(dataFull_, true)
    };
    this.flag_Unmount = false;
  }
  isVisible = () => {
    let data_full = this.state.dataFull;
    data_full.config.visible = false;
    this.setState({ dataFull: data_full });
  }
  btnRemoveRememberApp = () => {
    let data_full = this.state.dataFull;
    data_full.app_choose.isChoose = false;

    Application.autoGotoApp_set("");
    this.setState({ dataFull: data_full });
  }

  buildDataFromLang(dataFull, isStart) {
    dataFull.img_logo.value = Application.getLang("login_logo");
    dataFull.img_logo.hyper_link = Application.getLang("login_url_logo");

    dataFull.title = Application.getLang("login_welcome");
    dataFull.title_sub = Application.getLang("login_welcome_sub");
    dataFull.title_sub1 = Application.getLang("login_welcome_sub1");

    if (!isStart)
      dataFull.form_error.message = Application.getLang(this.state.form_error);

    dataFull.email.title = Application.getLang("login_user");
    dataFull.email.placeholder = Application.getLang("login_user_placeholder");

    dataFull.password.title = Application.getLang("login_pass");
    dataFull.password.placeholder = Application.getLang("login_pass_placeholder");

    dataFull.button_submit.dataFull.config.default.title = Application.getLang("login_signin");


    return dataFull;
  }
  componentWillUnmount() {
    this.flag_Unmount = true;
  }
  changeInputEmail = (event) => {
    let data_full = this.state.dataFull;
    data_full.email.value = event.target.value.toLowerCase();
    this.setState({ dataFull: data_full });
  }
  changeInputPassword = (event) => {
    let data_full = this.state.dataFull;
    data_full.password.value = event.target.value;
    this.setState({ dataFull: data_full });
  }
  changeLang = (dataItem, index) => {
    let data_full = this.state.dataFull;
    let data_ = data_full.lang.data;
    for (var i = 0; i < data_.length; i++) {
      let item_ = data_[i];
      if (item_.selected) {
        data_[i].selected = false;
        break;
      }
    }
    data_[index].selected = true;
    this.setState({ dataFull: data_full });

    //chuyển ngữ
    var all_lang = Application.getAllLang();
    if (all_lang[dataItem.key] === undefined) {
      postWebTxCode(
        [
          {
            txcode: "#sys:bo-load-jlang",
            input: { lang_data: dataItem.key },
          },
        ],
        {}
      ).then(
        (result) => {
          //luôn luôn chỉ 1 lang_data
          var lang_join = Application.foGetData("sys_lang_data", result);
          if (lang_join === undefined) lang_join = {};

          var lang_data_ = Application.foGetData("lang_data", result);
          if (lang_data_ !== undefined) for (var x in lang_data_) lang_join[x] = lang_data_[x];

          Application.addLang(dataItem.key, lang_join);
          Application.setLangKey(dataItem.key);
          this.callUpdateLang();
        },
        (error) => { }
      );
    } else {
      Application.setLangKey(dataItem.key);
      this.callUpdateLang();
    }
  }
  callUpdateLang = () => {
    let dataFull_ = this.state.dataFull;
    this.setState({ dataFull: this.buildDataFromLang(dataFull_) });
  }
  onLogin = () => {
    let data_full = this.state.dataFull;
    data_full.button_submit.dataFull.cmd.isLoading = true;
    data_full.button_submit.dataFull.cmd.isLock = true;

    this.setState({ dataFull: data_full }, () => {
      postWebTxCode([{
        txcode: "#sys:bo-projectLogin-signIn",
        input: {
          e: this.state.dataFull.email.value.trim(),
          p: this.state.dataFull.password.value.trim()
        }
      }], {}).then(
        result => {
          let status_login = Application.foGetData("login-status", result);
          let data_full_rs = this.state.dataFull;
          if (status_login !== undefined) {
            if (status_login.error_login !== undefined) {
              data_full_rs.form_error.message = Application.getLang(status_login.error_login);
              this.setState({ dataFull: data_full_rs, form_error: status_login.error_login });
            }
            else
              if (status_login.status_login !== undefined)
                if (status_login.status_login === "login#127")
                  this.isVisible();
          }

          //mở lại sau 1 thời gian chờ
          setTimeout(() => {
            if (!this.flag_Unmount) {
              data_full_rs.button_submit.dataFull.cmd.isLoading = false;
              data_full_rs.button_submit.dataFull.cmd.isLock = false;
              this.setState({ dataFull: data_full_rs });
            }
          }, 2000);
        },
        error => {
        }
      );
    });
  }
  render() {
    const ULogin = managerTemplate_getComponent("uLogin");
    return <ULogin dataFull={this.state.dataFull}></ULogin>;
  }
}
export default Login;