import wizA from "./wizA";
import Application from "./libSupport";
import libClient from "./libClient";
import { rsa_Encrypted, algorithm_response_quick } from "./libRSA";
//bo là gói tin truyền về server có kèm txCode
//context là gói thông tin của flow giúp việc chạy lại flow
export function postWebTxCode(bo, context) {
	return new Promise((resolve, reject) => {
		var wA = new wizA();

		//khởi tạo 1 key_feedback xuyên suốt quá trình bo fo
		let key_feedback = "";
		if (context["key_feedback"] !== undefined)
			key_feedback = context.key_feedback;
		else
			key_feedback = Application.autoUui_key_feedback();

		wA.setBo(bo, key_feedback);
		fetch(wA.info.u, {
			method: 'POST',
			body: rsa_Encrypted(JSON.stringify(wA.getForTxCode())),
			headers: {
				"Accept": "text/plain",
				"Content-Type": "text/plain; charset=UTF-8"
			}
		})
			.then(response =>
				response.text()
				//response.json() 
			).then(result => {
				if (result.substring(0, 1) === "{")
					result = JSON.parse(result);
				else {
					var time_ = result.substr(0, result.indexOf('-'));
					result = result.substr(result.indexOf('-') + 1);
					result = JSON.parse(algorithm_response_quick(result, time_));
				}

				if (result === undefined)
					return;

				libClient.fo(result.fo, context);
				resolve(result);
			})
			.catch(() => {
				//xử lý mở rộng
				reject(bo);
			});
	});
}