//lưu trữ tất cả thông tin loading
//có tính toàn cùng
var application_loading = {
    start: { DOM_Html: null, current: 0, max_pack: 12 },//khi mới mở
    header: null//cho header
}

export const start_add = (start_) => {
    application_loading.start.DOM_Html = start_;
}
export const start_percent = (str_type_data, system_count, name_info) => {
    if (application_loading.start.DOM_Html !== null) {
        application_loading.start.current++;
        application_loading.start.DOM_Html.setPercent(str_type_data, system_count, name_info, application_loading.start.max_pack, application_loading.start.current);
    }
}